<template>
  <div class="speech-rate-container">
    <!-- 顶部标题栏 -->
    <div class="header">
      <div class="back-button" @click="goBack">
        <img src="../assets/back.png" alt="返回" class="back-img" />
      </div>
      <div class="title">语音播报速度调整</div>
    </div>

    <!-- 聊天内容区域 -->
    <div class="chat-container">
      <!-- 老人消息 -->
      <div class="message-row right">
        <div class="message-content">
          <div class="message-bubble">
            <span class="old-message">5s</span>
            <div class="voice-icon">
              <img src="../assets/voice-icon1.png" alt="语音" />
            </div>
          </div>
        </div>
        <div class="avatar">
          <img src="../assets/avatar.png" alt="老人头像" />
        </div>
      </div>

      <!-- 医生消息 -->
      <div class="message-row left">
        <div class="avatar">
          <img src="../assets/doctor.png" alt="医生头像" />
        </div>
        <div class="message-content">
          <div class="message-bubble">
            <div class="voice-icon">
              <img src="../assets/voice-icon2.png" alt="语音" />
            </div>
            <span class="doctor-message">12s</span>
          </div>
        </div>
      </div>

      <!-- 医生消息 -->
      <div class="message-row left">
        <div class="avatar">
          <img src="../assets/doctor.png" alt="医生头像" />
        </div>
        <div class="message-content">
          <div class="message-bubble">
            <div class="voice-icon">
              <img src="../assets/voice-icon2.png" alt="语音" />
            </div>
            <span class="doctor-message">22s</span>
          </div>
        </div>
      </div>

      <!-- 老人消息 -->
      <div class="message-row right">
        <div class="message-content">
          <div class="message-bubble">
            <span class="old-message">15s</span>
            <div class="voice-icon">
              <img src="../assets/voice-icon1.png" alt="语音" />
            </div>
          </div>
        </div>
        <div class="avatar">
          <img src="../assets/avatar.png" alt="老人头像" />
        </div>
      </div>
    </div>

    <!-- 播放速度调节 -->
    <div class="speed-control">
      <div class="speed-title">播放速度</div>
      <div class="speed-slider-container">
        <div class="slow-icon">
          <img src="../assets/slow-icon.png" alt="慢速" />
        </div>
        <div class="slider-track">
          <div class="slider-lines">
            <div class="slider-line" v-for="i in 6" :key="i" @click="setSpeed(i)"></div>
          </div>
          <div class="slider-thumb" :style="{left: thumbPosition + '%'}" @mousedown="startDrag"></div>
        </div>
        <div class="fast-icon">
          <img src="../assets/fast-icon.png" alt="快速" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SpeechRate',
  data() {
    return {
      fromReportView: false,
      currentSpeed: 3, // 默认速度级别 (1-6)
      isDragging: false
    }
  },
  computed: {
    thumbPosition() {
      // 将速度级别转换为百分比位置
      return (this.currentSpeed - 1) * 20;
    }
  },
  created() {
    // 获取来源信息
    this.fromReportView = this.$route.query.fromReportView === 'true';
  },
  mounted() {
    // 添加全局事件监听
    window.addEventListener('mousemove', this.onDrag);
    window.addEventListener('mouseup', this.stopDrag);
  },
  beforeUnmount() {
    // 移除全局事件监听
    window.removeEventListener('mousemove', this.onDrag);
    window.removeEventListener('mouseup', this.stopDrag);
  },
  methods: {
    goBack() {
      // 返回我的页面，并保持状态
      this.$router.push({
        path: '/my-page',
        query: { 
          fromReportView: this.fromReportView ? 'true' : 'false'
        }
      });
    },
    startDrag() {
      this.isDragging = true;
    },
    stopDrag() {
      this.isDragging = false;
    },
    onDrag(e) {
      if (!this.isDragging) return;
      
      // 获取滑块轨道的位置和宽度
      const track = document.querySelector('.slider-track');
      const rect = track.getBoundingClientRect();
      
      // 计算鼠标在轨道上的相对位置
      let position = (e.clientX - rect.left) / rect.width;
      position = Math.max(0, Math.min(1, position));
      
      // 将位置转换为速度级别 (1-6)
      this.currentSpeed = Math.ceil(position * 6);
      if (this.currentSpeed < 1) this.currentSpeed = 1;
      if (this.currentSpeed > 6) this.currentSpeed = 6;
    },
    setSpeed(level) {
      this.currentSpeed = level;
    }
  }
}
</script>

<style scoped>
